/* styles/components/_inputs.css */

input {
    outline: none;
    border: 1.5px solid rgba(var(--border-color), 0.15);
    border-radius: 1.5rem;
    padding: 0.5rem 0.65rem;
    color: var(--current-text);
    font-size: 1rem;
    background-color: transparent;
    transition: border 0.3s ease;
}

input:focus {
    border: 1.5px solid rgba(var(--border-color), 0.3);
}

/* #chat-footer:has(input:focus, textarea:focus) {
    border: 1.5px solid rgba(var(--border-color), 0.3);
} */

textarea {
    font-family: Arial, sans-serif;
    outline: none;
    border: none;
    resize: none;
    color: var(--current-text);
    font-size: 1rem;
    width: 100%;
    padding: 0.25rem;
    overflow-y: auto;
    background-color: transparent;
}


.tag-input {
    display: flex;
    flex-direction: column;
    border: 1px dashed rgba(var(--border-color), 0.5);
    padding: 5px;
    cursor: text;
    font-size: 0.8rem;
    border-radius: 5px;
    justify-content: center;
    width: 100%;
}

.tag-input .tag-options {
    display: flex;
}

.tag-input .tag-options input {
    border: none;
    outline: none;
    font-size: inherit;
    height: 2rem;
    min-width: min(6.25rem, 100%);
    width: 100%;
}

.tag-input button {
    font-size: 0.8rem;
    height: 2rem;
    min-width: 2rem;
    border-radius: 2px;
    padding: 0;
}

.tag-input .tags {
    display: flex;
    flex-wrap: wrap;
    max-height: 8.75rem;
    overflow-y: auto;
    margin-bottom: 4px;
    gap: 2px;
    scroll-behavior: smooth;
    position: relative;
}

.tag-input .tag {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgba(var(--current-button-hover), 0.1);
    color: var(--current-text);
    padding: 5px 8px;
    height: 1.5rem;
    border-radius: 2px;
    margin-bottom: 4px;
    width: fit-content;
    max-width: 45%;
    min-width: 3rem;
    cursor: pointer;
    user-select: none;
}

.tag-input .tag:hover {
    background: rgba(var(--current-button-hover), 0.2);
}

.tag-input .tag .tag-label {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.tag-input .tag .remove-tag {
    display: flex;
    align-items: center;
    margin-left: 5px;
}