/* styles/components/_menu.css */

.chat-menu.open+.chat-container,
.chat-menu.hover+.chat-container {
    /* 100% - width of chat-menu without closed */
    width: calc(100% - var(--menu-open-width));
}

@media (max-aspect-ratio: 1/1) {

    .chat-menu.open+.chat-container,
    .chat-menu.hover+.chat-container {
        width: calc(100% - 3.5rem);
    }
}

.chat-menu:not(.open)+.chat-container {
    /* 100% - (width of button + padding of .chat-menu * 1rem) */
    width: calc(100% - ((2.25 + 0.5 * 2) * 1rem));
}

.chat-menu {
    height: 100dvh;
    background-color: var(--conversations-theme);
    color: var(--current-text);
    position: relative;
    display: flex;
    flex: 0 0 auto;
    flex-direction: row;
    justify-content: left;
    align-items: flex-start;
    padding: 0.5rem;
    padding-bottom: 3.25rem;
    overflow: hidden;
    z-index: 100;
    transition: transform 0.4s ease, padding 0.4s ease;
    backdrop-filter: blur(50px);
}

.chat-menu:has(> child) {
    overflow-y: auto;
    min-width: var(--menu-open-width);
}

.conversations-menu-header {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    width: 100%;
    height: fit-content;
}

.chat-menu .head {
    display: flex;
    align-items: center;
    width: 100%;
    /* Height of the toggle-menu button */
    height: 2.25rem;
    gap: 0.2rem;
    transition: transform 0.4s ease;
}

#toggle-menu {
    position: fixed;
    top: 0.5rem;
    left: 0.5rem;
    display: flex;
    padding: 0.5rem;
    flex-direction: column;
    transition: transform 0.3s ease;
    z-index: 105;
}

#toggle-settings {
    position: fixed;
    bottom: 0.5rem;
    left: 0.5rem;
    display: flex;
    padding: 0.5rem;
    flex-direction: column;
    transition: transform 0.3s ease;
    z-index: 105;
}

#toggle-menu .hamburger-icon {
    position: relative;
    width: 80%;
    height: 70%;
}

#toggle-menu .hamburger-icon .line {
    width: 100%;
    height: 2px;
    background-color: var(--current-text);
    border-radius: 50rem;
    position: absolute;
    transition: all 0.3s ease;
    will-change: transform;
}

#toggle-menu .hamburger-icon .line-1 {
    top: 0;
    transition: all 0.5s ease;
}

#toggle-menu .hamburger-icon .line-2 {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#toggle-menu .hamburger-icon .line-3 {
    bottom: 0;
    transition: all 0.3s ease;
}

#toggle-menu:has(+.chat-menu.open) .hamburger-icon .line-1 {
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
}

#toggle-menu:has(+.chat-menu.open) .hamburger-icon .line-2 {
    width: 0;
}

#toggle-menu:has(+.chat-menu.open) .hamburger-icon .line-3 {
    top: 50%;
    transform: translateY(-50%) rotate(-45deg);
}

.conversations-menu-header .user-id-display {
    font-size: 0.75rem;
    text-align: center;
    display: flex;
    align-items: center;
    padding: 0.25rem 0;
    padding-bottom: 0.5rem;
}

.chat-menu-content {
    max-height: 100%;
    min-height: 5rem;
    width: 100%;
    overflow-y: auto;
}

.conversations {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    min-height: inherit;
}

.conversations-menu-contianer,
.settings-menu-container {
    min-height: 100%;
    transition: transform 0.4s ease;
}

.conversations .conversation {
    position: relative;
    padding: 0.35rem 0.4rem;
    font-size: 0.8rem;
    border-radius: 0.75rem;
    width: 100%;
    background-color: rgba(var(--border-color), 0);
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
}

.conversations .conversation.selected {
    background-color: rgba(var(--border-color), 0.15);
}

.chat-menu .search-conversations {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 0.25rem;
}

.chat-menu .search-conversations input {
    font-size: 0.8rem;
    padding: 0.5rem 0.75rem;
    padding-right: 2rem;
    width: 100%;
}

.chat-menu .search-conversations i {
    position: absolute;
    right: 0;
    display: flex;
    margin-right: 0.75rem;
    font-size: 0.8rem;
}

.conversations .conversation:not(.selected):hover {
    background-color: rgba(var(--border-color), 0.1);
    cursor: pointer;
}

.conversations .conversation:hover .conversation-options-button {
    visibility: visible;
    opacity: 1;
}

.conversations .conversation .conversation-options-button {
    padding: 0;
    min-width: 1.5rem;
    height: 1.5rem;
    font-size: 1rem;
    visibility: hidden;
    opacity: 0;
}

.conversations .conversation .conversation-name {
    width: 100%;
    height: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-left: 0.25rem;
}

.chat-menu:not(.open),
.chat-menu:not(.hover) {
    width: var(--conversations-width);
    max-width: var(--conversations-width);
    justify-content: left;
    padding-right: 3.5rem;
    /* transform: translateX(-14.75rem); */
    /* transform: translateX(-5.5rem); */
}

.chat-menu.open,
.chat-menu.hover {
    width: min(var(--conversations-width), 100%);
    min-width: min(13rem, 100dvw);
    max-width: var(--conversations-width);
    padding: 0.5rem;
    padding-bottom: 3.25rem;
    flex: 0 0 auto;
}

.chat-menu.cover {
    --menu-open-width: 100%;
    max-width: var(--menu-open-width);
}

.conversations-menu-header> :not(#toggle-menu) {
    opacity: 1;
    transition: opacity 0.5s ease;
}

.chat-menu.open>*:not(#toggle-menu),
.chat-menu.hover>*:not(#toggle-menu) {
    width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
}