/* styles/components/_chat.css */

.chat-container {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    right: 0;
    scroll-behavior: smooth;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    justify-content: space-between;
    width: calc(100% - var(--menu-open-width));
    height: 100%;
    transition: width 0.4s ease;
}

#chat-footer {
    position: relative;
    box-sizing: border-box;
    /* flex: 1 1 auto; */
    width: min(100%, 48rem);
    margin-top: 0;
    max-height: calc(100dvh - 8rem);
    padding: 1.2rem 1.05rem;
    padding-bottom: 0.6rem;
    border: 1.5px solid rgba(var(--border-color), 0.15);
    box-shadow: 0 4px 10px 0px rgba(var(--current-button-hover), 0.1);
    border-radius: 2rem;
    background-color: var(--current-theme);
    transition: border 0.3s ease;
}

#scroll-bottom {
    position: absolute;
    font-size: 0.7rem;
    top: -2.5rem;
    right: 50%;
    left: 50%;
    width: auto;
    height: auto;
    transform: translateX(-50%);
    display: flex;
    padding: 0.75rem 0.5rem;
    background-color: var(--current-theme);
    border: 1px solid rgba(var(--border-color), 0.2);
    transition: opacity 0.4s ease;
}

#scroll-bottom.hide:not(.scrolling) {
    opacity: 0;
    pointer-events: none;
}

#scroll-bottom:hover:not(.scrolling) {
    animation: scroll-bottom 1.5s infinite;
}

#scroll-bottom.scrolling {
    opacity: 1;
    transform: translate(-50%, 3px);
}

#scroll-bottom::before {
    content: '';
    z-index: 2;
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-radius: inherit;
}

#scroll-bottom:hover::before {
    background-color: rgba(var(--current-button-hover), 0.1);
}

.chat-footer-margin {
    position: absolute;
    bottom: 0;
    height: 4rem;
    width: 100%;
    background-color: var(--current-theme);
}

.chat-footer-container {
    position: sticky;
    display: flex;
    justify-content: center;
    width: min(95%, 48rem);
    padding-bottom: 1.5rem;
    bottom: 0;
}

#chat-footer .message-options {
    position: relative;
    width: 100%;
    height: 2.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: min(100%, 58rem);
}

.chat-body {
    flex: 1 1 auto;
    position: relative;
    display: flex;
    flex-direction: column;
    width: min(95%, 48rem);
    padding: 2rem 0;
}

.message-container {
    margin-bottom: 0.5rem;
    max-width: 80%;
    font-size: 0.8rem;
    position: relative;
}

.message-container .message {
    width: fit-content;
    max-width: 100%;
    min-width: 1rem;
    height: fit-content;
    padding: 0.65rem 0.85rem;
    border-radius: 1.15rem;
    position: relative;
}

.message-container.from-others {
    align-self: flex-start;
    color: var(--current-text);
}

.message-container.from-me {
    color: var(--current-text);
    display: flex;
    flex-direction: column;
    align-self: end;
}

.sent-time {
    max-width: 100%;
    font-size: 0.75rem;
    overflow-wrap: break-word;
    display: flex;
}

.message-container.from-me .sent-time {
    justify-content: right;
}

.message-container.from-others .sent-time {
    justify-content: left;
}

.sent-time.new-day {
    width: 100%;
    display: flex;
    justify-content: center;
    color: var(--current-text);
}

.message-container .sent-time p {
    overflow-wrap: anywhere;
    hyphens: auto;
}

.message-container .sent-time.hide {
    display: none;
}

.message-container .sender {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
}

.message-container.from-me .sender {
    flex-direction: row-reverse;
    justify-content: right;
    display: none;
}

.message-container.from-others .sender {
    flex-direction: row;
    justify-content: left;
    margin: 5px 0 4px 0;
}

.message-container .sender .profile-picture {
    width: 1.75rem;
    height: 1.75rem;
    background-color: rgba(var(--border-color), 0.15);
    border-radius: 50rem;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.message-container .sender .profile-picture img {
    width: inherit;
    height: inherit;
}

.message-container .sender .profile-picture i {
    font-size: 1.75rem;
    background-color: #ffffff;
}

.message-container.from-others .sent-time {
    margin-left: 1.5rem;
}

.message-container.from-others .message {
    background-color: var(--from-others-message-color);
    color: var(--current-text);
    margin-left: 1.5rem;
}

.message-container.from-me .message {
    background-color: var(--color-theme);
    color: var(--current-text);
    align-self: flex-end;
}

.message-container .message .label {
    max-width: 100%;
    overflow-wrap: break-word;
}