/* styles/main.css */

html {
    font-size: 16px;
}

:root {
    --dark: 30, 31, 35;
    --text-dark: 82, 84, 97;
    --light: 255, 255, 255;
    --text-light: 255, 255, 255;

    --button-hover: rgba(76, 76, 99, 0.05);
    --color-theme: #ccdef5;

    --menu-open-width: 18rem;
    --conversations-width: 100%;
}

:root {
    --current-theme: rgb(var(--light));
    --conversations-theme: #f4f4f4;
    --border-color: var(--text-dark);
    --current-text: rgb(var(--text-dark));
    --current-button-hover: var(--text-dark);
    --from-others-message-color: rgba(var(--dark), 0.1);
}

body.light {
    --current-theme: rgb(var(--light));
    --conversations-theme: #f4f4f4;
    --border-color: var(--text-dark);
    --current-text: rgb(var(--text-dark));
    --current-button-hover: var(--text-dark);
    --from-others-message-color: rgba(var(--dark), 0.1);
}

body.dark {
    --current-theme: rgb(var(--dark));
    --conversations-theme: #292a2e;
    --border-color: var(--text-light);
    --current-text: rgb(var(--text-light));
    --current-button-hover: var(--text-light);
    --from-others-message-color: rgba(var(--light), 0.1);
}

.no-transitions,
.no-transitions * {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -o-transition: none !important;
    transition: none !important;
}

.no-animations,
.no-animations * {
    -webkit-animation: none !important;
    -moz-animation: none !important;
    -o-animation: none !important;
    animation: none !important;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: Arial, sans-serif;
    background-color: var(--current-theme);
    scrollbar-width: thin;
    scrollbar-color: #e3e3e3 transparent;
    display: flex;
    flex-direction: row;
    position: relative;
    height: 100dvh;
}

