hr {
    width: 100%;
    margin: 8px 0;
    border: 0.75px solid rgba(var(--border-color), 0.2);
}


button {
    min-width: 2.25rem;
    width: auto;
    height: 2.25rem;
    border-radius: 100rem;
    padding: 1rem;
    font-size: 1.15rem;
    border: none;
    outline: none;
    color: var(--current-text);
    background-color: transparent;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    overflow: visible;
}

button input[type="file"] {
    display: none;
}

button>* {
    position: absolute;
}

button p {
    z-index: 2;
}

button:hover,
button:focus {
    background-color: rgba(var(--current-button-hover), 0.1);
}