/* styles/components/_buttons.css */


/*-------------------- OPTIONS --------------------*/
.option {
    position: relative;
    padding: 0.5rem;
    border-radius: 0.25rem;
    background-color: rgba(var(--border-color), 0.15);
    min-width: 5rem;
    display: flex;
    color: var(--current-text);
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;

    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
    justify-content: space-between;
}

.option .option-arrow {
    animation: open-dropdown 0.2s ease forwards;
}

.option.active .option-arrow {
    animation: close-dropdown 0.2s ease forwards;
}

.option ul {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;

    background-color: var(--current-bg);
    border: 1px solid #ccc;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 10;
    list-style-type: none;

    padding: 0;
    margin: 0;
    border-radius: 0 0 0.25rem 0.25rem;

    display: none;
}

.option li {
    padding: 0.5rem;
    cursor: pointer;
    margin: 0;
}

.option li:hover {
    background-color: rgba(var(--current-button-hover), 0.1);
}

.option.active ul {
    display: block;
}


/*-------------------- RIPPLE EFFECT --------------------*/
.ripple-button {
    position: relative;
    overflow: hidden;
}

.ripple-button p {
    position: relative;
    font-size: inherit;
    z-index: 2;
    margin: 0;
    padding: 0;
}

.ripple {
    --ripple-duration: 0.75s;

    position: absolute;
    border-radius: 50%;
    transform: scale(0);
    background-color: rgba(var(--current-button-hover), 0.1);
    pointer-events: none;
    opacity: 1;

    animation: ripple-grow var(--ripple-duration) ease-out forwards;

    transition: opacity 0.2s ease-out;
    z-index: 5;
}

.ripple.fading {
    opacity: 0;
}


/*-------------------- DROPDOWNS --------------------*/

.dropdown-btn {
    cursor: pointer;
}

.dropdown-btn:hover,
.dropdown-btn:focus {
    background-color: rgba(var(--current-button-hover), 0.1);
}

.dropdown-btn:hover,
.dropdown-btn:focus>* {
    color: var(--current-text);
}