}

@keyframes open-dropdown {
  from {
    transform: rotate(180deg);
  }

  to {
    transform: rotate(0);
  }
}

@keyframes close-dropdown {
  from {
    transform: rotate(360deg);
  }

  to {
    transform: rotate(180deg);
  }
}

@keyframes ripple-grow {
  to {
    transform: scale(3);
  }
}

@keyframes show-tooltip {
  0% {
    transform: scale(.5);
  }

  1% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes hide-tooltip {
  0% {
    transform: scale(1);
  }

  99% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scroll-bottom {
  0% {
    transform: translate(-50%);
  }

  40% {
    transform: translate(-50%);
  }

  45% {
    transform: translate(-50%, 3px);
  }

  57% {
    transform: translate(-50%, -3px);
  }

  65% {
    transform: translate(-50%, 3px);
  }

  70% {
    transform: translate(-50%);
  }

  100% {
    transform: translate(-50%);
  }
}

.tooltip {
  background-color: var(--current-text);
  color: var(--current-theme);
  white-space: nowrap;
  transform-origin: top;
  pointer-events: none;
  user-select: none;
  opacity: 0;
  border-radius: .25rem;
  padding: .25rem;
  font-size: .6rem;
  display: inline-block;
  position: absolute;
  bottom: 0;
}

.tooltip:not(.no-transitions) {
  transition: transform .2s, opacity .2s;
}

.tooltip.visible {
  opacity: 1;
  visibility: visible;
  animation: .2s forwards show-tooltip;
}

.tooltip:not(.visible) {
  opacity: 0;
