/* styles/components/_utilities.css */

.tooltip {
    position: absolute;
    bottom: 0;
    display: inline-block;
    font-size: 0.6rem;
    background-color: var(--current-text);
    color: var(--current-theme);
    padding: 0.25rem;
    border-radius: 0.25rem;
    white-space: nowrap;
    transform-origin: top;
    pointer-events: none;
    user-select: none;
    opacity: 0;
    /* transform: scale(0.2); */
}

.tooltip:not(.no-transitions) {
    transition: transform 0.2s ease, opacity 0.2s ease;
}

.tooltip.visible {
    opacity: 1;
    /* transform: scale(1); */
    visibility: visible;
    animation: show-tooltip 0.2s forwards;
}

.tooltip:not(.visible) {
    opacity: 0;
    /* transform: scale(0.2); */
    animation: hide-tooltip 0.2s forwards;
}

.tooltip p {
    color: var(--current-theme);
}

.dropdown-content,
.dropdown-subcontent {
    border: 1px solid rgba(var(--border-color), 0.2);
    display: block;
    visibility: hidden;
    pointer-events: none;
    position: absolute;
    background-color: var(--current-theme);
    min-width: 10rem;
    font-size: 0.7rem;
    border-radius: 0.9rem;
    padding: 0.25rem;
    box-shadow: 0rem 0.25rem 0.5rem 0rem rgba(var(--border-color), 0.1);
    z-index: 107;
}

.dropdown-subcontent {
    z-index: 108;
}

.dropdown-content.show,
.dropdown-subcontent.show,
.dropdown-subcontent.click-show {
    visibility: visible;
    pointer-events: auto;
}

.dropdown-content i,
.dropdown-subcontent i {
    margin: 0 0.5rem 0 0;
}

.dropdown-content a,
.dropdown-content span,
.dropdown-subcontent a,
.dropdown-subcontent span {
    display: flex;
    align-items: center;
    color: var(--current-text);
    padding: 0.5rem 0.65rem;
    border-radius: 0.6rem;
    text-decoration: none;
    user-select: none;
    cursor: pointer;
}

.dropdown-content a .subcontent-chevron,
.dropdown-subcontent a .subcontent-chevron {
    margin: 0 0 0 auto;
    font-size: 0.8rem;
}

